import React from 'react'

const FormInput = ({label, type, value, handleChange, require=null}) => {
  return (
    <div className="pb2">
      <div className="dib-ns w-25-ns"><label className="f5-ns f6">{label}{require ? ' *' : ''}</label></div>
      <div className="dib-ns w-75-ns"><input required={require} value={value} onChange={handleChange} type={type} className="w-100 h2  bw0 br2 b--mid-grey"  /></div>
    </div>
  )
}

const FormText = ({label, value, handleChange, require=null}) => {
  return (
    <div className="pb2">
      <div className="dib-ns w-25-ns v-top"><label className="f5-ns f6">{label} {require ? '*' : ''}</label></div>
      <div className="dib-ns w-75-ns"><textarea required={require} value={value} onChange={handleChange} className="bw0 br2 w-100 mw-100 h3" /></div>
    </div>
  )
}

const Contact = ({name, mail, msg, handleNameChange, handleMailChange, handleMsgChange, handleSubmit}) => {
  return (
    <form onSubmit={(event) => handleSubmit(event)}>
      <FormInput
        label="Name"
        type="text"
        value={name}
        handleChange={handleNameChange}
      />
      <FormInput
        label="E-mail"
        type="email"
        value={mail}
        handleChange={handleMailChange}
        require="require"
      />
      <FormText
        label="Message"
        value={msg}
        handleChange={handleMsgChange}
        require="require"
      />
      <div>
        <div className="dib w-25-ns"></div>
        <div className="dib">
          <input 
            type="submit"
            value="Submit"
            className="btn-solid-reg btn-icon-send"
          />
        </div>
      </div>
    </form>
  )
}

export default Contact
