import React, { useState } from 'react'
import axios from 'axios'
import Contact from './components/ContactForm'
import Notification from './components/Notification'

function App() {
  const [ name, setName ] = useState('')
  const [ mail, setMail ] = useState('')
  const [ msg, setMsg ] = useState('')
  const [ status, setStatus ] = useState(null)

  const resetFrom = () => {
    setName('')
    setMail('')
    setMsg('')
  }

  const showNotification = (message, type='success') => {
    setStatus({ message, type })
    setTimeout(() => {
      setStatus(null)
    }, 10000);
  }

  const handleSubmit = (event) => {
    event.preventDefault()

    axios({
      method: 'POST',
      url: 'http://localhost:3002/api/send',
      data: {name, mail, msg}
    }).then((response) => {
      if (response.data.status === 'success') {
        showNotification('Thank you, your message has been sent!')
      } else {
        showNotification('Oops, something went wrong...', 'error')
        console.log('error', response.data)
      }
    }).catch(error => {
      showNotification('Oops, something went wrong: ' + error.message, 'error')
      console.log(error)
    })
    window.scrollTo(0, 0)
    resetFrom()
  }
  
  return (
    <div>
      <div className='mt4-ns mw6 mw7-ns center system-sans-serif'>
        <div>
          <Notification msg={status} closeNotification={() => setStatus(null)} />
        </div>
        <div className='bg-light-gray pa3 ph4-ns br4-ns'>
          <div className='mw8 ph3 pv2'>
            <Contact 
              name={name}
              mail={mail}
              msg={msg}
              handleNameChange={(event) => setName(event.target.value)}
              handleMailChange={(event) => setMail(event.target.value)}
              handleMsgChange={(event) => setMsg(event.target.value)}
              handleSubmit={handleSubmit}
            /> 
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
