import React from 'react'
import { FaTimesCircle } from 'react-icons/fa';

const Notification = ({ msg, closeNotification }) => {
  if (msg === null) {
    return null
  }

  return (
    
    <div className={msg.type==='success' ? 'pa2 mb2 mb3-ns ph4-ns bg-green bw1 b--solid-ns br2-ns' : 'pa2 mb2 mb3-ns ph4-ns bg-light-red bw1 b--solid-ns br2-ns'}>
      <div className='mw-100 ph3 pv2'>
        <div className='dib' style={{maxWidth: "80%"}}>{msg.message}</div>
        <div className='dib fr mw3' style={{maxWidth: "20%"}} onClick={closeNotification}><FaTimesCircle /> Close</div>
      </div>
    </div>
  )
}

export default Notification
